<template>
    <div class="vendor-block" @mouseover="stop" @mouseout="autoPlay">
      <div class="vd-blockWidth">
        <section class="vd-arrowBlock" @click="openPlatform = !openPlatform">
          <img src="../../assets/icon/arrow-down.png" alt="" v-if="!openPlatform">
          <img src="../../assets/icon/arrow-up.png" alt="" v-if="openPlatform">
        </section>
        <div class="narrow narrow-left" v-if="!openPlatform && platform.length >= 5" @click="prevImg">
          <img src="../../assets/icon/narrow1.png" alt="" >
        </div>
        <section class="vd-layout vd-layout1" :class="{active: !openPlatform}">
          <ul class="clearfix" ref="carouser">
            <li v-for="(item,index) in platform" :key="index">
              <div>
                <img :src="item.img">
              </div>
              <span>{{item.name}}</span>
            </li>
          </ul>          
        </section>
        <div class="narrow narrow-right" v-if="!openPlatform && platform.length >= 5"  @click="nextImg">
          <img src="../../assets/icon/narrow2.png" alt="" >
        </div>
        <section class="vd-layout vd-layout2" :class="{active: openPlatform}">
          <ul class="clearfix">
            <li v-for="(item,index) in platform" :key="index">
              <div>
                <img :src="item.img">
              </div>
              <span>{{item.name}}</span>
            </li>
          </ul>
        </section>
      </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            openPlatform: false,
            platform: [
                {
                img : require('../../assets/icon/vendor/vendor-1.png'),
                name : '1MG电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-2.png'),
                name : 'CQ9电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-3.png'),
                name : 'PP电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-4.png'),
                name : 'PG电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-5.png'),
                name : '捕鱼游戏'
                },
                {
                img : require('../../assets/icon/vendor/vendor-6.png'),
                name : '6MW电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-7.png'),
                name : 'PP电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-8.png'),
                name : 'PT电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-3.png'),
                name : '9PP电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-4.png'),
                name : '10PG电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-5.png'),
                name : '11捕鱼游戏'
                },
                {
                img : require('../../assets/icon/vendor/vendor-6.png'),
                name : '12MW电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-7.png'),
                name : '13PP电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-8.png'),
                name : '14PT电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-6.png'),
                name : '15MW电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-7.png'),
                name : '16PP电子'
                },
                {
                img : require('../../assets/icon/vendor/vendor-8.png'),
                name : '17PT电子'
                },
            ],
            offsetCount: 0,
        }    
    },
    methods:{
        prevImg() {
            if (this.platform.length - 5 > 0) {
                let group = Math.ceil(this.platform.length / 5) 
                if (this.offsetCount > 0) {
                this.offsetCount --;
                // console.log(this.offsetCount)          
                this.$refs.carouser.style.left = '-' + (1100*this.offsetCount) + 'px'
                } else {
                if (this.offsetCount == 0) {  
                    this.offsetCount += (group-1);
                    // console.log(this.offsetCount)          
                    this.$refs.carouser.style.left = '-' + (1100*this.offsetCount) + 'px'
                }
                //  return false	
                }			
            } else {
                return false				
            }
        },
        nextImg() {
            if (this.platform.length - 5 > 0) {
                let group = Math.ceil(this.platform.length / 5) 
                if (this.offsetCount < group-1) { 
                // console.log(group) 
                this.offsetCount ++;
                // console.log(this.offsetCount)          
                this.$refs.carouser.style.left = '-' + (1100*this.offsetCount) + 'px'
                } else if(this.offsetCount == group-1) {
                // console.log('abc')
                this.offsetCount -= (group-1);
                // console.log(this.offsetCount)  
                this.$refs.carouser.style.left = '-' + (1100*this.offsetCount) + 'px'
                } else {
                return false				
                }
            } else {
                return false				
            }
        },
        autoPlay() {
            this.carouselTimer = setInterval (this.nextImg, 5000)
        },
        stop() {
          clearInterval(this.carouselTimer)
        }
    },
    mounted(){
        this.autoPlay()
    }
}
</script>

