<script>
export default {
    name: 'Footer',
    data() {
        return {
            ftSliders : [
                {img : require('../../assets/slot/slot01.png')},
                {img : require('../../assets/slot/slot02.png')},
                {img : require('../../assets/slot/slot03.png')},
                {img : require('../../assets/slot/slot04.png')},
                {img : require('../../assets/slot/slot05.png')},
                {img : require('../../assets/slot/slot06.png')},
                {img : require('../../assets/slot/slot07.png')},
                {img : require('../../assets/slot/slot08.png')},
                {img : require('../../assets/slot/slot09.png')},
                {img : require('../../assets/slot/slot10.png')},
                {img : require('../../assets/slot/slot11.png')},
                {img : require('../../assets/slot/slot12.png')},
                {img : require('../../assets/slot/slot13.png')},
            ],
            offsetCount: 0,
            ftHistoryFixed : true,
        }
    },
    computed:{
    },
    methods:{
        prevImg() {
            // if (this.ftSliders.length - 10 > 0) {
            //     if (this.offsetCount > 0) {
            //         this.offsetCount -= 700;			
            //         console.log(this.offsetCount)				
            //         this.$refs.carouser.style.left = '-' + (1*this.offsetCount)+ 'px';					
            //     } else {
            //         return false
            //     }			
            // } else {
            //     return false				
            // }
            if (this.ftSliders.length - 10 > 0) {
                let group = Math.ceil(this.ftSliders.length / 10) 
                if (this.offsetCount > 0) {
                    this.offsetCount --;
                    // console.log(this.offsetCount)          
                    this.$refs.carouser.style.left = '-' + (700*this.offsetCount) + 'px'
                } else {
                if (this.offsetCount == 0) {  
                    this.offsetCount += (group-1);
                    // console.log(this.offsetCount)          
                    this.$refs.carouser.style.left = '-' + (700*this.offsetCount) + 'px'
                }
                //  return false	
                }			
            } else {
                return false				
            }
        },
        nextImg() {
            // if (this.ftSliders.length - 10 > 0) {
            //     if (this.offsetCount < this.ftSliders.length - 10) {
            //         this.offsetCount += 700;	
            //         console.log(this.offsetCount)				
            //         this.$refs.carouser.style.left = '-' +  (1*this.offsetCount) + 'px'				
            //     } else {
            //         return false
            //     }			
            // } else {
            //     return false				
            // }
            if (this.ftSliders.length - 10 > 0) {
                let group = Math.ceil(this.ftSliders.length / 10) 
                if (this.offsetCount < group-1) { 
                    // console.log(group) 
                    this.offsetCount ++;
                    // console.log(this.offsetCount)          
                    this.$refs.carouser.style.left = '-' + (700*this.offsetCount) + 'px'
                    } else if(this.offsetCount == group-1) {
                    // console.log('abc')
                    this.offsetCount -= (group-1);
                    // console.log(this.offsetCount)  
                    this.$refs.carouser.style.left = '-' + (700*this.offsetCount) + 'px'
                } else {
                    return false				
                }
            } else {
                return false				
            }
        },
        handleScroll() {
            var scrollTop = document.documentElement.scrollTop || document.body.scrollTop //距顶部
            var windowHeight = document.documentElement.clientHeight || document.body.clientHeight //可视区高度
            var scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight //滚动条总高度
            if(scrollTop + windowHeight >= scrollHeight -250){
                this.ftHistoryFixed = false
            } else {
                return this.ftHistoryFixed = true
            }
        }
    },
    mounted () {
        window.addEventListener('scroll', this.handleScroll)
    },
    destroyed () {
        window.removeEventListener('scroll', this.handleScroll)
    }
}
</script>

