<script>
export default {
  name: 'Header',
  delimiters: ['${', '}'],
  data() {
    return {
      start : 8246391.00,
      end : 8357401.00,
    }
  },
  mounted(){
    this.countTo();
  },
  methods: {
    countTo() {  
      let _this = this;
      let start = _this.start;
      setInterval( () => {
        _this.start += 0.05
        if(_this.start > _this.end) {
          return start
        }
      }, 1000);
    }    
  },
  filters: {
    numFilter(value) {
      var toFixedNum = Number(value).toFixed(3);
      var realVal = toFixedNum.substring(0, toFixedNum.toString().length - 1);
      return realVal;
    },
    commaFormat: function(value) { // 加上千分位符號
      return value.toString().replace(/^(-?\d+?)((?:\d{3})+)(?=\.\d+$|$)/, function (all, pre, groupOf3Digital) {
        return pre + groupOf3Digital.replace(/\d{3}/g, ',$&');
      })
    }
  }
}

